.post-container {
  display: flex;
  justify-content: space-between;
  color: #222830;
  margin-top: 70px;
  flex-direction: column;
}
.post-container div:nth-child(2) {
  width: 35vw;
}
.post-image {
  width: 40vw;
  height: 17vw;
  border-radius: 6px;
  object-fit: cover;
}
.post-date {
  font-size: 0.8rem;
  margin-top: 8px;
}
.post-title {
  margin-top: 10px;
  font-size: 1.4rem;
  color: #222830;
}
.post-description {
  margin-top: 10px;
  color: #646464;
}
