.featured-post-container {
  display: flex;
  justify-content: space-between;
  color: #222830;
  margin-top: 70px;
  flex: 1;
}
.box-featured-post {
  display: flex;
  flex-direction: column;
}
.featured-post-image {
  width: 47vw;
  height: 23vw;
  border-radius: 6px;
  margin-right: 30px;
  object-fit: cover;
}
.featured-post-date {
  font-size: 0.8rem;
}
.featured-post-title {
  margin-top: 20px;
  color: #222830;
}
.featured-post-description {
  margin-top: 20px;
  color: #646464;
}
